import React, { Component } from 'react'
import styled from 'styled-components'

const StyledPara = styled.p`
  color: grey;
`

class ColumnTally extends Component {
  render() {
    const cart = this.props.cart
    const itemsToRender = cart.filter(
      cartItem => cartItem.confirmationStatus === 'UNCONFIRMED',
    )
    const filt = itemsToRender.length

    return (
      <div>
        <StyledPara>
          {filt} of{' '}
          {me.cart.reduce((tally, cartItem) => tally + cartItem.quantity, 0)}
        </StyledPara>
      </div>
    )
  }
}

export default ColumnTally
